import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useSearchParams } from 'react-router-dom'

import type { SelectChangeEvent } from '@mui/material'
import {
  CircularProgress,
  Container,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  MenuItem,
  Pagination,
  Select,
  Stack,
  Switch,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import CLUB_BELONGING from 'src/data/belonging'
import CLUB_CATEGORIES, { type ClubCategories } from 'src/data/categories'
import { CLUB_SEARCH_MESSAGE, COMMON_MESSAGE } from 'src/data/messages'
import {
  RECRUIT_SORTING_MAPPING,
  RECRUIT_SORTING_ORDERS,
  type RecruitSortingOrders
} from 'src/data/recruitNoticeSortingOrders'

import EmptySearchResult from 'src/components/EmptySearch'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import RecruitNoticePreview, { type RecruitNoticePreviewProps } from 'src/components/RecruitNoticePreview'
import Searchbox from 'src/components/Searchbox'

import { type SearchRecruitsResponseBody } from 'src/responses/search'

const SELECT_COMPONENT_WIDTH = '160px'

const RecruitNoticePage = () => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))
  const [searchParams, setSearchParams] = useSearchParams()
  const [isLoading, setIsLoading] = useState(false)
  const [recruits, setRecruits] = useState<RecruitNoticePreviewProps[]>([])
  const [numPages, setNumPages] = useState(1)
  const [params, setParams] = useState({
    name: searchParams.get('name') ?? '',
    category: (searchParams.get('category') as ClubCategories | '') ?? '',
    belonging: searchParams.get('belonging') ?? '',
    isApplicableRecruit: searchParams.has('isApplicableRecruit') ? true : null,
    page: searchParams.has('page') ? parseInt(searchParams.get('page') || '1') : null,
    order: searchParams.get('order') ?? ''
  })
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()
  const token = authHeader()

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = event => {
    event.preventDefault()
    const search = document.getElementById('search') as HTMLInputElement
    setParams(prev => ({ ...prev, name: search.value, page: null, order: '' }))
  }
  const handleSelectBoxChange = ({
    target: { name, value }
  }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    setParams(prev => ({ ...prev, category: name === 'type' ? '' : prev.category, page: null, [name]: value }))
  }
  const handleSwitchChange = ({ target: { name } }: React.ChangeEvent<HTMLInputElement>, checked: boolean) => {
    setParams(prev => ({ ...prev, page: null, [name]: checked ?? false }))
  }
  const handlePageChange = (event: React.ChangeEvent<HTMLInputElement | unknown>, value: number) =>
    setParams(prev => ({ ...prev, page: value === 1 ? null : value }))
  const handleClickCategory: React.MouseEventHandler<HTMLButtonElement> = event => {
    const category = event.currentTarget.getAttribute('data-value') as ClubCategories

    if (!CLUB_CATEGORIES.includes(category)) {
      enqueueSnackbar(CLUB_SEARCH_MESSAGE.WRONG_CATEGORY, { variant: 'warning' })
      return
    }
    setParams(prev => ({ ...prev, category: category }))
  }

  useEffect(() => {
    const newParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.belonging ? { type: params.belonging } : null,
      params.page ? { page: params.page } : null,
      params.isApplicableRecruit ? { isApplicableRecruit: params.isApplicableRecruit } : null,
      // params.orderBy ? { orderBy: params.orderBy } : null,
      params.order ? { order: params.order } : null
    )
    setSearchParams(newParams)
  }, [params])

  useEffect(() => {
    setIsLoading(true)
    const order = params.order as RecruitSortingOrders | null
    const apiParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.belonging ? { belonging: params.belonging } : null,
      params.isApplicableRecruit ? { isApplicableRecruit: params.isApplicableRecruit } : null,
      params.page ? { page: params.page } : null,
      { count: 12 },
      order ? { ...RECRUIT_SORTING_MAPPING[order] } : null
    )
    axios
      .get<SearchRecruitsResponseBody>(`${process.env.REACT_APP_APIHOST}/search/recruits`, {
        params: apiParams,
        headers: { Authorization: token }
      })
      .then(res => {
        setNumPages(res.data?.numPages ?? 1)
        setRecruits(res.data?.recruits ?? [])
        setIsLoading(false)
      })
      .catch(err => {
        enqueueSnackbar(err.response.data?.message ?? COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
        setIsLoading(false)
      })
  }, [searchParams])

  return (
    <>
      <Helmet>
        <title>모집 공고</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Stack direction="row" alignItems="baseline">
              <Typography variant="h3" component="h3" gutterBottom sx={{ marginRight: 1 }}>
                모집 공고
              </Typography>
            </Stack>
            <Typography variant="subtitle2">키워드와 카테고리, 분류를 활용해 원하는 동아리를 찾아보세요</Typography>
          </Grid>
        </Grid>
        <Searchbox
          handleSubmit={handleSubmit}
          searchDefaultValue={params.name}
          filterCategory={params.category}
          handleClickCategory={handleClickCategory}
        />
      </PageTitleWrapper>
      <Container sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        <Grid container width="100%" direction={isNotMobile ? 'row' : 'column'} spacing={1}>
          {token && (
            <FormControl size="small">
              <FormControlLabel
                control={
                  <Switch
                    name="isApplicableRecruit"
                    checked={params.isApplicableRecruit ?? false}
                    onChange={handleSwitchChange}
                    color="primary"
                  />
                }
                label="지원 가능 공고만 보기"
                labelPlacement="start"
              />
            </FormControl>
          )}
          <Grid item sx={{ marginLeft: isNotMobile ? 'auto' : 0 }}>
            <FormControl size="small" fullWidth={!isNotMobile}>
              <InputLabel id="belonging-label">소속</InputLabel>
              <Select
                labelId="belonging-label"
                id="belonging"
                name="belonging"
                label="소속"
                value={params.belonging}
                onChange={handleSelectBoxChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {CLUB_BELONGING &&
                  CLUB_BELONGING.map((name, idx) => (
                    <MenuItem key={idx} value={name}>
                      {name}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item>
            <FormControl size="small" fullWidth={!isNotMobile}>
              <InputLabel id="order-label">정렬</InputLabel>
              <Select
                labelId="order-label"
                id="order"
                name="order"
                label="정렬"
                value={params.order ?? ''}
                onChange={handleSelectBoxChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">선택하지 않음</MenuItem>
                {RECRUIT_SORTING_ORDERS &&
                  Object.entries(RECRUIT_SORTING_ORDERS).map(([key, value]) => (
                    <MenuItem key={key} value={key}>
                      {value}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        {isLoading ? (
          <CircularProgress size={64} disableShrink thickness={3} />
        ) : recruits.length ? (
          <Grid container spacing={2} marginTop={1}>
            {recruits.map(recruit => (
              <Grid item xs={12} sm={12} md={6} key={recruit.id}>
                <RecruitNoticePreview {...recruit} />
              </Grid>
            ))}
          </Grid>
        ) : (
          <EmptySearchResult />
        )}

        {recruits.length !== 0 && (
          <Pagination
            page={params.page ?? 1}
            count={numPages ?? 1}
            size={isNotMobile ? 'medium' : 'small'}
            onChange={handlePageChange}
            sx={{ my: 3 }}
          />
        )}
      </Container>
    </>
  )
}

export default RecruitNoticePage
